.editable-cell {
  position: relative;
  text-align: center;
}

.total-qty {
  padding: 5px 12px;
}

.editable-cell-value-wrap {
  border: 1px solid #d9d9d9;
  border-radius: 4px;
  padding: 4px 11px;
  cursor: pointer;
}

.error {
  border: 1px solid #f5222d;
}

